<template>
  <section class="skill">
    <img class='viewer-img' :src='`/img/game/${data.skillpic}.jpg`' />
    <div class='viewer-description'>
      <h1>{{data.name}}</h1>
      <p>{{data.description}}</p>
      <p>
        cost:
        <span v-for='(c, k) in costs' :key='k' :class='`viewer-energy ${c}`'></span>
      </p>
    </div>
  </section>
</template>

<script>
export default {
  props: ["data"],
  computed: {
    costs() {
      const energies = ["speed", "strength", "elemental", "wisdom", "random"];
      let cost = [];
      for (let i = 0; i < energies.length; i++) {
        for (let key = 0; key < this.data.cost[i]; key++) {
          cost.push(energies[i]);
        }
      }
      return cost;
    },
  },
};
</script>


<style lang="scss" scoped>
.skill {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: left;
    align-items: center;
}
</style>